}

/*BACK TO POSITION*/
.back-to-position {
  opacity: 0;
  transition: 1s;
  overflow: hidden;
}

.back-to-position.to-right {
  transform: translateX(-7vw);
}

.back-to-position.to-left {
  transform: translateX(7vw);
}

.back-to-position.to-top {
  transform: translateY(100px);
}

.back-to-position.to-bottom {
  transform: translateY(-100px);
}

.show .back-to-position {
  opacity: 1;
  transform: translateX(0);
}

//spd
.show .back-to-position.spd.delay-1 {
  transition-delay: .2s;
}

.show .back-to-position.spd.delay-2 {
  transition-delay: .4s;
}

.show .back-to-position.spd.delay-3 {
  transition-delay: .6s;
}

.show .back-to-position.spd.delay-0 {
  transition-delay: .05s;
}



.show .back-to-position.spd.delay-1 {
  transition-delay: .2s;
}

.show .back-to-position.delay-0 {
  transition-delay: .3s;
}

.show .back-to-position.delay-1 {
  transition-delay: .6s;
}

.show .back-to-position.delay-2 {
  transition-delay: 0.8s;
}

.show .back-to-position.delay-3 {
  transition-delay: 1.3s;
}

.show .back-to-position.delay-4 {
  transition-delay: 1.6s;
}

.show .back-to-position.delay-5 {
  transition-delay: 1.8s;
}

.show .back-to-position.delay-6 {
  transition-delay: 2.0s;
}

.show .back-to-position.delay-7 {
  transition-delay: 2.2s;
}

.show .back-to-position.delay-8 {
  transition-delay: 1s;
}

/* HEADER */
header {
  font-family: 'Secular One', sans-serif;
  position: relative;
  width: 100%;
  top: 0;
  background-color: white;
  height: 110px;
  position: fixed;
  z-index: 9;
  /* top:0; */
  border-bottom: 4px solid rgb(69, 48, 138);

  .inner {
    max-width: 1100px;
    height: 110px;
    margin: 0 auto;

    .logo {
      height: 42px;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 10px;
      margin: auto;

      img {
        width: 117.5px;
        height: 42px;
      }
    }

    .main__nav {
      width: 700px;
      height: 50px;
      position: absolute;
      top: 60px;
      right: -80px;

      .nav {
        position: relative;
        display: flex;
        text-align: right;
        line-height: 1.4;

        .nav__name {
          font-size: 20px;
          font-weight: bold;
          display: block;
          padding: 0 15px;
          margin: 0 20px;
          z-index: 1;
          height: 20px;
          white-space: nowrap;

          a {
            position: relative;
            color: black;
          }

          a::after {
            content: "";
            position: absolute;
            left: 50%;
            bottom: -4px;
            width: 0;
            height: 3px;
            background-color: rgb(69, 48, 138);
            transition: all .6s;
            /* border-radius: 0 10px 10px 0; */
          }

          a::before {
            content: "";
            position: absolute;
            right: 50%;
            bottom: -4px;
            width: 0;
            height: 3px;
            background-color: rgb(69, 48, 138);
            transition: all .6s;
            /* border-radius: 10px 0 0 10px; */
          }

          a:hover {
            color: rgba(33, 33, 33, 0.7);
          }

          a:hover::after {
            width: 50%;
          }

          a:hover::before {
            width: 50%;
          }

          #home {
            color: rgb(69, 48, 138);
          }

          .solutions-nav {
            /* display:none; */
            visibility: hidden;
            opacity: 0;
            margin: 10px 0 0 0;
            background-color: white;
            transition: all .5s;

            li {
              font-size: 16px;
              border: 2px solid rgb(69, 48, 138);
              padding: 4px 4px 4px 0;
              font-weight: bold;
              text-align: center;

              li a::after {
                content: "";
                position: absolute;
                left: 10px;
                bottom: -1px;
                width: 0;
                height: 2px;
                background-color: rgb(69, 48, 138);
                transition: all .6s;
              }

              a::before {
                content: "";
                position: absolute;
                right: 50%;
                bottom: -4px;
                width: 0;
                height: 3px;
                background-color: rgb(69, 48, 138);
                transition: all .6s;
              }

              a:hover::after {
                width: 50%;
              }

              a:hover:before {
                width: 50%;
              }
            }

          }

        }

        .nav__name:hover {
          color: rgba(33, 33, 33, 0.7);
        }

        .nav__name:hover .solutions-nav {
          /* display:block; */
          visibility: visible;
          opacity: 1;
          color: black;
        }
      }
    }
  }

  .nav__button {
    position: absolute;
    right: 15px;
    width: 48px;
    font-size: 24px;
    display: none;
    /* top:30px; */
    top: 0;
    bottom: 0;
    margin: auto 0;
  }
}

